    /**
     * 开启流量
     * @param event
     */
    var openFluxChangeHandler = function(event){
        if(event.detail){
            var detail = event.detail;
            //[{id:1,flux:{}}]
            var link = self.findLink(detail.id);
            if(link){
                link.flux = detail.flux;
                if(link.ins)
                    link.ins.render();
            }
        }else{
            self.links.forEach(function(link){
                delete link.flux;
                if(link.ins)
                    link.ins.render();
            });
        }
    }

    /**
     * 开启告警
     * @param event
     */
    var openAlarmChangeHandler = function(event){
        if(event.detail){
            //[{id:1,type:"link/node",value:{}}]
            var detail = event.detail;
            switch(detail.type){
                case "link":{
                    var link = self.findLink(detail.id);
                    if(link){
                        link.value = detail.value;
                        if(link.ins)
                            link.ins.render();
                    }
                    break;
                }
                case "node":{
                    var node = self.findNode(detail.id);
                    if(Global.isDebug) {
                        console.log(node);
                    }
                    if(node){
                        node.value = detail.value;
                        if(Global.isDebug) {
                            console.log(node);
                        }
                        if(node.ins)
                            node.ins.render();
                    }
                    break;
                }
            }
        }else{
            self.links.forEach(function(link){
                if(Global.isDebug) {
                    console.log(link);
                }
                
                delete link.value;
                if(link.ins)
                    link.ins.render();
            });
            self.nodes.forEach(function(node){
                if(Global.isDebug) {
                    console.log(node);
                }
                delete node.value;
                if(node.ins)
                    node.ins.render();
            });
        }
    }

