    BaseElement.apply(this,arguments);
    var self  = this;
    self.svgElement = _element.append("path");
    var superRenderFunc = this.render;
    /**
     * border形状终结butt，round，square
     */
    var linecap="butt";
    this.linecap = function(value){
        if (!arguments.length)
            return linecap;
        linecap = value;
        return this;
    }


    /**
     * 透明度
     * @type {number}
     */
    var opacity=1;
    this.opacity = function(value){
        if (!arguments.length)
            return opacity;
        opacity = value;
        return this;
    }
