    /**
     * 创建框选矩形
     */
    var createSelectRect = function() {
        if(self.selectCon) {
            var con = self.selectCon;
            var rect = con.select("rect.select");
            if(rect.node() === null) {
                con.append("rect")
                    .attr("class", "select")
                    .attr("x", startX)
                    .attr("y", startY)
                    .attr("stroke", "#0078d7")
                    .attr("stroke-width", 1)
                    .attr("fill", "#0078d7")
                    .attr("fill-opacity", 0.1);
            }
        }
    };
    
    /**
     * 绘制框选矩形
     */
