//全局对象
var Global = {};

//---release config start
//是否测试版本，发布时注意更改
Global.isDebug = false;
//是否用于ev可视化平台，发布时注意更改
Global.isUseByEVPlatform = true;
//---release config end

//可视化平台编辑器中，当前是否处于编辑页面
Global.isEVPlatformEditPage = window.location.href.indexOf('visual/stage') !== -1;

//外部插件映射关系
Global.outerPlugins = {
	componentMap:{},
	chartMap:{},
	layoutMap:{},
	pluginMap:{},
	nodeMap:{},
	linkMap:{}
};

module.exports = Global;


