    /**
     * 用来进行图表数据所在键值对的键名称，比如"{values:[data]}"中的values，(默认值"")，
     * 若不传，则keyPath属性无效，且使用图表数据固定的key值，比如topo数据用nodes和links
     */
    var key = "";
    this.key = function(value) {
        if (!arguments.length) 
            return key;
        key = value;
        return this;
    };
    
    //------------------------------
