    //------------------------------
    
    /**
     * 本图表在series chart中的索引值(默认值-1)
     */
    var seriesIndex = -1;
    this.seriesIndex = function(value) {
        if (!arguments.length) 
            return seriesIndex;
        seriesIndex = value;
        return this;
    };
    
    //------------------------------
