    //  Attributes
    //
    //--------------------------------------------------------------------------
    
    //------------------------------
    //  model
    //------------------------------
    
    /**
     * 水平布局模型
     */
    var model = null;
    this.model = function(){
        return model;
    };
    
    //------------------------------
    //  horizontalAlign
    //------------------------------
    
    /**
     * 水平对齐方式，left|center|right(默认值left)
     */
    var horizontalAlign = "left";
    this.horizontalAlign = function(value) {
        if (!arguments.length) return horizontalAlign;                
        horizontalAlign = value;
        return this;
    };
    
    //------------------------------
    //  verticalAlign
    //------------------------------
    
